.section {

  &-title {
    margin: 0;
    margin-bottom: 48px;
    font-size: 32px;
    font-weight: 500;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: center;
    text-transform: capitalize;

    &--align--left {
      text-align: left;
    }

    @include on-tablet {
      font-size: 48px;
    }

    @include on-desktop {
      font-size: 64px;
    }
  }
}

